<template>
  <div class="songlist">
    <p>
      <span><slot></slot></span>
      <router-link to="/songListMore">更多></router-link>
    </p>
    <div class="list">
      <ul>
        <li v-for="(item, index) in imgItem" :key="index">
          <router-link :to="`/particulars/${item.id}`">
            <span
              ><i>{{ item.playCount | formatNum }}</i></span
            >
            <img :src="item.picUrl+'?param=140y140'" alt="" />
            <p>{{ item.name }}</p>
          </router-link>
        </li>
      </ul>
      <Loading v-if="isLoading"></Loading>
    </div>
  </div>
</template>

<script>
import Loading from "@/components/Loading";
export default {
  name: "SongList",
  components: {
    Loading
  },
  data() {
    return {
      isLoading: true,
      imgItem: [],
    };
  },
  filters: {
    formatNum(value) {
      return (value / 10000).toFixed(1) + "万";
    },
  },

  created() {
    this.$axios
      .get("/personalized")
      .then((data) => {
        return data.data.result;
      })
      .then((item) => {
        this.imgItem = item;
        this.isLoading=false;

      });
  },

};
</script>
<style lang="less" scoped>
.songlist {
  height: 220px;
  padding: 0 3%;
  color: #454545;
  background-color: white;
  border-radius: 0 0 20px 20px;
  p {
    width: 100%;
    span {
      font-size: 1rem;
      font-weight: 600;
      float: left;
    }
    a {
      float: right;
      color: #636363;
      font-size: 0.9rem;
      border: 1px solid #e6e6e6;
      border-radius: 20px;
      padding: 2px 6px;
    }
  }
  // 隐藏滚动条
  .list::-webkit-scrollbar {
    display: none;
  }
  .list {
    clear: both;
    padding-top: 14px;
    width: 100%;
    height: 170px;
    overflow: hidden;
    overflow-x: auto;
    ul {
      width: (120px * 6)+ (10 * 6);
      li {
        float: left;
        width: 120px;
        height: 170px;
        overflow: hidden;
        margin-right: 10px;
        position: relative;
        img {
          display: block;
          border-radius: 14px;
          width: 120px;
        }
        p {
          width: 118px;
          text-align: left;
          font-size: 0.7rem;
          padding: 4px;
          color: #454545;
        }
        span {
          position: absolute;
          top: 0;
          right: 0;
          font-size: 0.8rem;
          margin: 5px;
          padding: 2px 4px;
          padding-left: 20px;
          border-radius: 20px;
          background-color: rgba(126, 123, 123, 0.6);
          i {
            font-style: normal;
            color: rgb(236, 236, 236);
          }
          &::before {
            content: "";
            width: 20px;
            height: 21px;
            position: absolute;
            left: 2px;
            top: 0;
            background-image: url(../../assets/images/icon/songlisticon.png);
            background-repeat: no-repeat;
            background-size: 80%;
            background-position: center;
          }
        }
      }
    }
  }
}
</style>
