<template>
  <div class="box">
    <div class="shade" v-if="searchBarFixed == true"></div>
    <div class="playMent" :class="searchBarFixed == true ? 'isFixed' : ''">
      <div @click="$emit('newSongList',dataNewSong)">
        <img src="../../assets/images/icon/playMent.png" alt="" />
        <p>播放全部<span>(最新音乐)</span></p>
      </div>
      <!-- <span><img src="../../assets/images/icon/onMent.png" alt=""/></span> -->
    </div>
    <div class="list">
      <ul>
        <li
          v-for="(item, index) in data"
          @click="
            $emit(
              'play',
              {
                al: {
                  picUrl: item.picUrl,
                },
                name: item.name,
                ar: [{ name: item.song.artists[0].name }],
                id: item.id,
              },
              (isActive = index)
            )
          "
          :key="item.id"
          :style="{ borderColor: isActive == index ? 'red' : '' }"
        >
          {{ item.song.artists.name }}
          <span
            class="left"
            :style="{ color: isActive == index ? 'red' : '' }"
            >{{ index + 1 }}</span
          >
          <div class="content">
            <p>{{ item.name }}</p>
            <p>{{ item.song.album.artists[0].name }} - {{ item.song.name }}</p>
          </div>
          <div class="icon">
            <span :class="isActive == index ? 'red' : ''"></span>
          </div>
        </li>
      </ul>
    </div>
    <Loading v-if="isLoading"></Loading>
  </div>
</template>
<style lang="less" scoped>
.box {
  background-color: white;
  padding-bottom: 120px;
  margin-top: 10px;
  border-radius: 20px 20px 0 0;
  .shade {
    width: 100%;
    height: 40px;
  }
  .isFixed {
    position: fixed;
    background-color: white;
    top: 50px;
    // height: 40px;
    // width: 94%!important;
  }
}

.playMent {
  width: 100%;
  height: 48px;
  box-sizing: border-box;
  padding: 3%;
  display: flex;
  div{
    flex: 1;
    img {
      float: left;
      width: 26px;
      margin-right: 14px;
    }
    p {
      line-height: 26px;
      text-align: left;
      font-weight: 600;
      color: #333;
      span {
        font-size: 0.8rem;
        margin-left: 10px;
        color: #afafaf;
      }
    }
  }
  span {
    width: 20%;
    height: 100%;
    img {
      width: 20px;
      margin-top: 4px;
      margin-right: 10px;
      float: right;
    }
  }
}
.list {
  padding: 3%;
  box-sizing: border-box;
  ul {
    width: 100%;
    li {
      height: 64px;
      color: #7a7a7a;
      border-bottom: 1px solid white;
      span.left {
        float: left;
        width: 10%;
        font-size: 1.2rem;
        line-height: 64px;
        height: 100%;
        font-weight: 600;
      }
      div.content {
        width: 78%;
        height: 100%;
        float: left;
        padding-left: 4px;
        p {
          height: 50%;
          text-align: left;
          vertical-align: bottom;
          overflow: hidden;
          font-size: 0.5rem;
          &:first-child {
            font-size: 0.9rem;
            line-height: 38px;
            color: #333;
          }
        }
      }
      div.icon {
        height: 100%;
        width: 10%;
        float: right;
        span {
          width: 100%;
          height: 32px;
          display: inline-block;
          margin-top: 16px;
          background-image: url(../../assets/images/icon/detail.png);
          background-repeat: no-repeat;
          background-position: right;
        }
        span.red {
          background-image: url(../../assets/images/icon/detail_red.png);
        }
      }
    }
  }
}
</style>
<script>
import Loading from "@/components/Loading";

export default {
  name: "NewSong",
  components: { Loading },
  data() {
    return {
      data: [],
      //   playData: '11112'
      searchBarFixed: false,

      isActive: null,
      isLoading: true,
      playMusice: {
        al: {
          picUrl: "",
        },
        name: "",
        ar: [],
        id: 111,
        dataNewSong: [],
      },
    };
  },
  created() {
    this.$axios
      .get("/personalized/newsong")
      .then((data) => {
        return data.data.result;
      })
      .then((item) => {
        this.data = item;
        this.isLoading = false;
        // this.playMusice = {
        //   al: {
        //     picUrl: item.picUrl
        //   },
        //   name: item.name,
        //   ar: [item.song.album.artists.name],
        //   id: item.id,
        // };
        let dataArr=[];
        for (let i = 0; i < this.data.length; i++) {
          dataArr.push({
            al: {
              picUrl: this.data[i].picUrl,
            },
            name: this.data[i].name,
            ar: [{ name: this.data[i].song.artists[0].name }],
            id: this.data[i].id,
          });
        }
        this.dataNewSong=dataArr;
      });
  },
  methods: {

    // handleScroll() {
    //   var scrollTop =
    //     window.pageYOffset ||
    //     document.documentElement.scrollTop ||
    //     document.body.scrollTop;
    //   var oft = document.querySelector(".home").clientHeight-900;
    //   // console.log(oft);
    //   if (scrollTop > oft) {
    //     this.searchBarFixed = true;
    //   } else {
    //     this.searchBarFixed = false;
    //   }
    // },
  },

  // mounted() {

  //   window.addEventListener("scroll", this.handleScroll);
  // },
};
</script>
