<template>
  <div class="banner" @mouseenter="enter" @mouseleave="leave">
    <ul>
      <li v-for="(item, index) in imgUrl" :key="index">
        <router-link to="">
          <img v-show="curIndex == index" :src="item.imageUrl+'?param=540y200'" alt="" />
          <span
            v-show="curIndex == index"
            :style="{ background: item.titleColor }"
            >{{ item.typeTitle }}</span
          >
        </router-link>
      </li>
    </ul>

    <ol>
      <li
        v-for="index in imgUrl.length"
        @click="curIndex = index - 1"
        :key="index"
        :style="curIndex == index - 1 ? ' background-color: aquamarine' : ''"
      >
        {{ index }}
      </li>
    </ol>
    <Loading v-if="isLoading"></Loading>
  </div>
</template>

<script>
import Loading from "@/components/Loading";
export default {
  name: "Banner",
  components: {Loading},
  data() {
    return {
      imgUrl: [],
      curIndex: 0,
      isLoading: true,
      timer: null,
    };
  },
  created() {
    this.$axios
      .get("/banner")
      .then((data) => {
        return data.data.banners;
      })
      .then((item) => {
        this.imgUrl = item;
        this.isLoading=false;
      });
    // 每次进入界面时，先清除之前的所有定时器，然后启动新的定时器
    clearInterval(this.timer);
    this.timer = null;
    this.setTimer();
  },
  methods: {
    enter() {
      clearInterval(this.timer);
      this.timer = null;
    },
    leave() {
      clearInterval(this.timer);
      this.timer = null;
      this.setTimer();
    },
    setTimer() {
      if (this.timer == null) {
        this.timer = setInterval(() => {
          this.curIndex++;
          if (this.curIndex == 10) this.curIndex = 0;
        }, 2000);
      }
    },
  },
  destroyed() {
    // 每次离开当前界面时，清除定时器
    clearInterval(this.timer);
    this.timer = null;
  },
};
</script>

<style lang="less" scoped>
.banner {
  width: 100%;
  padding: 3% 3% 0 3%;
  box-sizing: border-box;
  position: relative;
  background-color: white;
  ul {
    width: 100%;
    li {
      width: 100%;
      img {
        border-radius: 14px;
        width: 100%;
        display: block;
      }
    }
  }
  ol {
    position: absolute;
    bottom: 4%;
    left: 50%;
    transform: translateX(-50%);
    li {
      float: left;
      width: 10px;
      height: 10px;
      background-color: white;
      border-radius: 50%;
      text-indent: -9999px;
      margin-right: 5px;
    }
  }
  span {
    position: absolute;
    bottom: 0;
    right: 3%;
    padding: 5px;
    color: #fff;
    font-size: 0.8rem;
    border-radius: 10px 0px 10px 0px;
  }
}
</style>
