<template>
  <div :class="['playPage', { paused: paused }]">
    <audio
      v-if="playMusice"
      autoplay
      :src="
        `https://music.163.com/song/media/outer/url?id=${playMusice.id}.mp3`
      "
      @timeupdate="timeupdate"
      @play="play"
      @pause="pause"
      @ended="ppEnd"
      ref="player"
    ></audio>
    <div
      class="miniBar"
      v-if="playMusice"
      :style="{ bottom: !flag ? '0px' : '80px' }"
    >
      <div class="shade">
        <img
          :src="playMusice.al.picUrl"
          :style="{ animationPlayState: paused ? 'paused' : 'running' }"
          width="50"
          height="50"
          alt=""
        />
      </div>
      <p @click="isFull = true">
        <marquee
          direction="left"
          behavior="scroll"
          scrollamount="3"
          loop="1"
          hspace="10"
          vspace="50"
          width="150px"
        >
          <font
            >{{ playMusice.name }}
            <span> - {{ playMusice.ar[0].name }}</span></font
          >
        </marquee>
      </p>
      <div>
        <canvas
          width="50"
          @click="togglePlay"
          height="50"
          ref="miniPlay"
        ></canvas>
        <span class="btn" @click="togglePlay"></span>
      </div>
    </div>
    <div class="fullScreen" ref="fullScreen" v-if="isFull">
      <div class="top">
        <span class="exit" @click="isFull = false"></span>
        <div class="content">
          <p>{{ playMusice.name }}</p>
          <p>{{ playMusice.ar[0].name }}</p>

          <router-link
            :to="{
              path: '/Discuss/',
              query: { url: '/comment/music?id=', id: playMusice.id },
            }"
            class="right"
            tag="div"
          >
          </router-link>
          <!-- <route-view></route-view> -->
        </div>
      </div>
      <div class="banner">
        <div class="poin">
          <p>
            <img
              src="../assets/images/styli2.png"
              :class="paused ? 'left' : 'right'"
              alt=""
            />
          </p>
        </div>
        <div class="port">
          <div>
            <img
              :src="playMusice.al.picUrl + '?param=140y140'"
              :style="{ animationPlayState: paused ? 'paused' : 'running' }"
            />
          </div>
        </div>
      </div>

      <div class="lyric" @touchstart="yiru" @touchend="yichu">
        <ul ref="ulLyric">
          <div v-if="isLoading">
            <Loading></Loading>
            <p>歌词加载中...</p>
          </div>
          <li
            ref="lyric"
            v-for="(ly, index) in lyric"
            :key="index"
            @click="clickTime($event, ly.time)"
            :class="currentIndexs == index ? 'active' : ''"
          >
            {{ ly.lyric }}
          </li>
        </ul>
      </div>
      <div class="control">
        <div class="range">
          <span class="left"
            >{{ Math.floor(currentTime / 60) > 9 ? "" : "0"
            }}{{ Math.floor(currentTime / 60) }}:{{
              Math.floor(currentTime) % 60 > 9 ? "" : "0"
            }}{{ Math.floor(currentTime) % 60 }}</span
          >
          <input
            type="range"
            max="100"
            v-model="timeRange"
            :style="{ backgroundSize: timeRange + '%' }"
            @input="changeTime"
            step="0.1"
          />
          <span class="right" v-if="!duration">00:00</span>
          <span class="right" v-if="duration"
            >{{ Math.floor(duration / 60) > 9 ? "" : "0"
            }}{{ Math.floor(duration / 60) }}:{{
              Math.floor(duration) % 60 > 9 ? "" : "0"
            }}{{ Math.floor(duration) % 60 }}</span
          >
        </div>
        <div class="btn">
          <span @click="modeFun" :class="playMode[modeIndex]">循环播放</span>
          <span class="prev" @click="prevMusic">上一首</span>
          <span
            class="play"
            @click="togglePlay"
            :class="paused ? 'switch_1' : 'switch_2'"
            >播放</span
          >
          <span class="next" @click="nextMusic">下一首</span>
          <span class="list" @click="isShow = !isShow">列表</span>
        </div>
      </div>
      <ListPage
        v-if="isShow"
        @isShow="isShow = false"
        :data="{ musicList, playMusice }"
        @listPageEmit="$emit('listPageEmit', $event)"
        @delListPage="$emit('delListPage', $event)"
      ></ListPage>
    </div>
  </div>
</template>

<script>
import Loading from "@/components/Loading";
import ListPage from "@/components/ListPage";
export default {
  name: "Play",
  props: {
    playMusice: {
      type: Object,
      default: function() {
        return null;
      },
    },
    musicList: Array,
    flag: Boolean,
  },
  data() {
    return {
      paused: true,
      isFull: false,
      lyric: [],
      isLoading: true,
      timeRange: 0,
      duration: 0,
      currentTime: 0,
      isShow: false,
      modeIndex: 2,
      playMode: ["circle", "rand", "liebiao"],
      currentIndexs: 0,
      height: 0,
      moveTop: 0,
      isLyric: true,
      lyricTime: null,
    };
  },
  watch: {
    playMusice(value) {
      this.$axios.get("/lyric?id=" + value.id).then((d) => {
        let lyric = d.data.lrc.lyric;
        let lyricArray = lyric.split("\n");
        lyricArray.pop();
        let reg = /\[(\d+):(\d+\.\d+)\](.*)/;

        let newLiric = lyricArray.map((d) => {
          if (reg.test(d)) {
            return {
              time: parseInt(RegExp.$1) * 60 + parseFloat(RegExp.$2),
              lyric: RegExp.$3,
            };
          }
        });
        this.lyric = newLiric;
        // console.log(newLiric);
        this.isLoading = false;
      });
    },
  },
  methods: {
    yiru() {
      this.isLyric = false;
      clearTimeout(this.lyricTime);
    },
    yichu() {
      this.lyricTime = setTimeout(() => {
        this.isLyric = true;
      }, 2000);
    },
    modeFun() {
      this.modeIndex++;
      if (this.modeIndex == 3) this.modeIndex = 0;
    },
    ppEnd() {
      this.playEnd();
      if (this.playMode[this.modeIndex] != "circle") this.nextMusic();
    },
    playEnd() {
      if (this.playMode[this.modeIndex] == "circle") {
        //单曲循环
        this.$refs.player.play();
      } else if (this.playMode[this.modeIndex] == "rand") {
        //随机播放
        let randIndex = Math.floor(Math.random() * this.musicList.length);

        let randMusic = this.musicList[randIndex];
        this.$emit("play", randMusic);
      } else if (this.playMode[this.modeIndex] == "liebiao") {
        //列表播放
      }
      // this.prevMusic();
    },
    prevMusic() {
      let playId = this.playMusice.id;
      let prevMusic = null;
      this.musicList.filter((d, index) => {
        if (d.id == playId) {
          let i = --index;
          if (i < 0) i = this.musicList.length - 1;
          prevMusic = this.musicList[i];
        }
      });
      this.$emit("play", prevMusic);
      this.playEnd();
    },
    nextMusic() {
      let playId = this.playMusice.id;
      let nextMusic = null;
      this.musicList.filter((d, index) => {
        if (d.id == playId) {
          let i = ++index;
          if (i > this.musicList.length - 1) i = 0;
          nextMusic = this.musicList[i];
        }
      });
      this.$emit("play", nextMusic);
      this.playEnd();
    },
    pause() {
      this.paused = true;
      // this.start();
    },
    play() {
      this.paused = false;
      // this.stop();
    },
    togglePlay() {
      let player = this.$refs.player;
      if (this.paused) {
        player.play();
      } else {
        player.pause();
      }
    },
    changeTime(e) {
      let val = e.target.value;
      let player = this.$refs.player;
      let ctime = (val / 100) * this.duration;
      player.currentTime = ctime;
    },
    clickTime(e, val) {
      let player = this.$refs.player;
      player.currentTime = val;
    },
    timeupdate(e) {
      let audio = e.target;
      let currentTime = audio.currentTime;
      this.currentTime = currentTime;
      let duration = audio.duration;
      this.duration = duration;
      this.drawCircle(currentTime, duration);
      this.timeRange = (currentTime / duration) * 100;
      for (let i = 0; i < this.lyric.length; i++) {
        if (i >= this.lyric.length - 1) {
          this.currentIndexs = i;
          break;
        }
        if (
          this.lyric[i].time <= this.currentTime &&
          this.currentTime < this.lyric[i + 1].time
        ) {
          this.currentIndexs = i;
          if (this.isLyric) {
            if (
              this.$refs.lyric != undefined &&
              this.$refs.lyric.length != 0 &&
              this.$refs.lyric != null
            ) {
              this.$refs.ulLyric.scrollTop =
                this.$refs.lyric[i].offsetTop -
                this.$refs.fullScreen.clientHeight / 1.5;
            }
          }
          break;
        }
      }
    },
    drawCircle(currentTime, duration) {
      if (this.$refs.miniPlay != undefined) {
        let canvas = this.$refs.miniPlay;
        let context = canvas.getContext("2d");
        context.clearRect(0, 0, 50, 50);
        context.beginPath();
        context.strokeStyle = "rgba(120,120,120,0.2)";
        context.lineWidth = 1; //线宽度
        context.arc(25, 25, 20, 0, 2 * Math.PI);
        context.stroke();
        context.closePath();
        let arcP = (currentTime / duration) * 2 * Math.PI;
        context.beginPath();
        context.strokeStyle = "rgba(120,120,120,0.8)";
        context.lineWidth = 2;
        context.arc(25, 25, 20, 0, arcP);
        context.stroke();
        context.closePath();
      }
    },
  },
  components: { Loading, ListPage },
  created() {},
};
</script>
<style lang="less" scoped>
.loading {
  margin-top: 40px;
}
.playPage {
  .miniBar {
    display: flex;
    position: fixed;
    bottom: 80px;
    width: 100%;
    background-color: white;
    z-index: 222;
    box-sizing: border-box;
    align-items: center;
    padding: 0 3%;
    .shade {
      background-color: #333;
      background-size: 100%;
      border-radius: 50%;
      position: relative;
      img {
        width: 40px;
        height: 40px;
        position: absolute;
        top: 50%;
        left: 50%;
        border-radius: 50%;
        animation: randImg 3s linear 0s infinite;
      }
    }

    p {
      flex: 1;
      font-size: 0.8rem;
      margin-left: 10px;
      text-align: left;
      padding: 10px 0;
      span {
        font-size: 0.4rem;
        color: rgb(144, 144, 144);
      }
    }
    div {
      width: 50px;
      height: 50px;
      position: relative;
      font-size: 0.5rem;
      .btn::after {
        width: 50px;
        height: 50px;
        left: 0;
        line-height: 50px;
        top: 0px;
        content: "暂停";
        position: absolute;
      }
    }
  }
  .fullScreen {
    position: fixed;
    width: 100%;
    height: 100vh;
    z-index: 334;
    bottom: 0;
    padding: 3%;
    box-sizing: border-box;
    background: linear-gradient(
      rgb(82, 135, 156),
      rgb(89, 177, 184),
      rgb(78, 115, 129)
    );
    div.top {
      // background-color: darkcyan;
      position: relative;
      div.right {
        position: absolute;
        right: 0;
        top: 0px;
        width: 28px;
        height: 28px;
        background-image: url(../assets/images/icon/comment_white.png);
        background-size: cover;
        // opacity: .5;
      }
      span.exit {
        width: 32px;
        height: 32px;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        background-image: url(../assets/images/icon/returnKey_1.png);
        background-size: 100%;
      }
      div.content {
        display: inline-block;
        p {
          &:nth-child(1) {
            font-size: 1rem;
            color: white;
            margin-bottom: 5px;
          }
          &:nth-child(2) {
            font-size: 0.9rem;
            color: rgb(223, 223, 223);
          }
        }
      }
    }
    div.banner {
      width: 100%;
      height: 50vh;

      div.poin {
        height: 14vh;
        p {
          height: 160%;
        }
        img {
          height: 100%;
          transform-origin: center 4vh;
        }
        img.left {
          animation: poin 0.8s linear 0s forwards reverse;
        }
        img.right {
          animation: poin 0.8s linear 0s forwards;
        }
      }
      div.port {
        width: 100%;
        div {
          height: 30vh;
          width: 30vh;
          border-radius: 50%;
          background-color: #222222;
          box-shadow: 0px 0px 28px #8887cc;
          position: relative;
          margin: 0 auto;
          z-index: -1;
          img {
            height: 72%;
            width: 72%;
            position: absolute;
            top: 50%;
            left: 50%;
            animation: randImg 6s linear 0s infinite;
            border-radius: 50%;
            margin: 0 auto;
          }
        }
      }
    }
    .lyric {
      width: 100%;
      height: 20vh;
      overflow: hidden;
      ul::-webkit-scrollbar {
        display: none;
      }
      ul {
        width: 100%;
        height: 200px;
        margin: 0 auto;
        overflow-x: auto;
        li::-webkit-scrollbar {
          display: none;
        }
        li.active {
          font-size: 1.1rem;
          color: red;
        }
        li {
          width: 60%;
          word-wrap: break-word;
          margin: 0 auto;
          color: rgba(1, 1, 1, 0.5);
          &:first-child {
            padding-top: 30px;
          }
          &:last-child {
            padding-bottom: 60px;
          }
        }
      }
    }
    .control {
      position: absolute;
      width: 94%;
      height: 14vh;
      bottom: 0;
      .range {
        height: 30px;
        span {
          color: rgb(241, 239, 239);
          font-size: 0.5rem;
          line-height: 30px;
        }
        span.left {
          float: left;
        }
        span.right {
          float: right;
        }
        input[type="range"] {
          width: 74vw;
          height: 2px;
          -webkit-appearance: none;
          background: -webkit-linear-gradient(red, red) no-repeat, #ddd;
        }

        input[type="range"]::-webkit-slider-thumb {
          -webkit-appearance: none; /*清除系统默认样式*/
          height: 10px; /*拖动块高度*/
          width: 10px; /*拖动块宽度*/
          background: white; /*拖动块背景*/
          border-radius: 50%; /*外观设置为圆形*/
          border: solid 1px rgb(190, 190, 190); /*设置边框*/
        }
      }
      .btn {
        height: 60%;
        display: flex;
        span {
          opacity: 0.7;
          width: 32px;
          height: 100%;
          flex: 1;
          display: inline-block;
          text-indent: -9999px;
          margin: 0 auto;
          background-repeat: no-repeat;
          background-position: center;
        }
        span.prev {
          background-image: url(../assets/images/icon/prevKey.png);
        }
        span.play {
          background-size: 50px;
          opacity: 0.8;
        }
        span.switch_1 {
          background-image: url(../assets/images/icon/playKey.png);
        }
        span.switch_2 {
          background-image: url(../assets/images/icon/stopKey.png);
        }
        span.next {
          background-image: url(../assets/images/icon/prevKey.png);
          transform: rotate(180deg);
        }

        span.circle {
          background-image: url(../assets/images/icon/circleKey.png);
        }
        span.rand {
          background-image: url(../assets/images/icon/randKey.png);
        }
        span.liebiao {
          background-image: url(../assets/images/icon/pattern.png);
        }
        span.list {
          background-image: url(../assets/images/icon/listKey.png);
        }
      }
    }
  }
}
.paused {
  .miniBar {
    // .shade {
    //   img {
    //     animation-play-state: paused;
    //   }
    // }

    div {
      .btn::after {
        content: "播放";
      }
    }
  }
}
@keyframes randImg {
  from {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  to {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}
@keyframes poin {
  from {
    transform: rotate(-30deg);
  }
  to {
    transform: rotate(-3deg);
  }
}
</style>
