<template>
  <div id="app">
    <div id="nav">
      <!-- <BottomNav></BottomNav>  -->
    </div>
    <transition
      enter-active-class="animate__animated animate__fadeInLeftBig"
      leave-active-class="animate__animated animate__fadeInRight"
    >
      <Play
        :flag="flag"
        :playMusice="playMusice"
        :musicList="musicList"
        @listPageEmit="playMusic"
        @delListPage="delListPage"
        @play="changeMusic"
      ></Play>
    </transition>

    <router-view @play="playMusic" @flag="flagFun" @addMusicList="addMusic" />
  </div>
</template>
<script>
import Play from "@/views/Play";
export default {
  components: {
    Play,
  },
  data() {
    return {
      flag: false,
      musicList: [],
      playMusice: {
        al: {
          picUrl:
            "https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=3331507852,3139460390&fm=26&gp=0.jpg",
        },
        name: "暂无歌曲播放",
        ar: ["1111"],
        id: 111,
      },
    };
  },

  methods: {
    addMusic(e) {
      this.musicList=[];
      for (let i = 0; i < e.length; i++) {
        this.musicList.push(e[i]);
      }
      this.playMusice = e[0];
    },
    changeMusic(e) {
      this.playMusice = e;
    },
    delListPage(e) {
      for (let i = 0; i < this.musicList.length; i++) {
        if (e.id == this.musicList[i].id) {
          this.musicList.splice(i, 1);
          if (this.musicList.length == 0) {
            break;
          }
        }
        if (e.id == this.playMusice.id) {
          this.playMusice = this.musicList[0];
        }
      }
    },
    playMusic(e) {
      this.playMusice = e;
      let exit = false;
      for (let i = 0; i < this.musicList.length; i++) {
        if (this.musicList[i].id == e.id) {
          exit = true;
          break;
        }
      }
      if (!exit) {
        this.musicList.unshift(e);
      }
    },
    flagFun(e) {
      this.flag = e;
      // console.log(this.flag);
    },
  },
};
</script>
